import uniIcons from '../unis/uni-icons/uni-icons.vue'
// import selectMenu from './selectMenu.vue'
/**
 * Pagination 分页器
 * @description 分页器组件，用于展示页码、请求数据等
 * @tutorial https://ext.dcloud.net.cn/plugin?id=32
 * @property {String} prevText 左侧按钮文字
 * @property {String} nextText 右侧按钮文字
 * @property {Number} current 当前页
 * @property {Number} total 数据总量
 * @property {Number} size 每页数据量
 * @property {Number} showIcon = [true|false] 是否以 icon 形式展示按钮
 * @event {Function} change 点击页码按钮时触发 ,e={type,current} current为当前页，type值为：next/prev，表示点击的是上一页还是下一个
 */

export default {
	name: 'UniPagination',
	components: {
		uniIcons,
		// selectMenu
	},
	props: {
		prevText: {
			type: String,
			default: '上一页'
		},
		nextText: {
			type: String,
			default: '下一页'
		},
		current: {
			type: [Number, String],
			default: 1
		},
		total: { // 数据总量
			type: [Number, String],
			default: 0
		},
		pageSize: { // 每页数据量 默认初始
			type: [Number, String],
			default: 3
		},
		showIcon: { // 是否以 icon 形式展示按钮
			type: [Boolean, String],
			default: false
		},
		sizes:{  //每页数据量 可选数组
			type:[Array]
		},
		tmpTableId:[Number,String]
	},
	data() {
		return {
			currentIndex: 1,
			// showSizes:false,
			// showCurrent:false,
			dsize:'' //记录当前设置的每页数据量
		}
	},
	mounted(){
		// document.addEventListener('click', (e) => {
		// 	 // console.log(this.$el.contains(e.target))
		// 	 if (!this.$el.contains(e.target)) {
		// 		 console.log("hide")
		// 		 this.closeSelect()
		// 		 // if(e.target.nodeValue)
				
		// 	}
		// 	else{
		// 		// let type = e.target.innerHTML;
		// 		// console.log(e.target.innerHTML)
		// 		//  if(type.indexOf('条')>-1){
		// 		// 	 this.showSelectFun()
		// 		//  }else{	
		// 		// 	 this.showCurrentFun()
		// 		//  }
				
		// 		// console.log("show")
				
		// 	}
		// })
	},
	computed: {
		popshow(){
			let pop = this.$store.state.table.popshow;
			if(pop && pop.tmpTableId == this.tmpTableId){
				return pop
			}
		},
		showSizes(){
			if(this.popshow && this.popshow.popname == 'sizes'){
				return true
			}
		},
		showCurrent(){
			if(this.popshow && this.popshow.popname == 'current'){
				return true
			}
		},
		color(){
			return this.$store.state.color;
		},			
		maxPage() {
			let maxPage = 1
			let total = Number(this.total)
			let size = Number(this.size)
			if (total && size) {
				maxPage = Math.ceil(total / size)
			}
			return maxPage
		},
		maxPageArr(){
			if(this.maxPage){
				let arr=[];
				for(let n=1;n<this.maxPage+1;n++){
					arr.push(n);
				}
				return arr;
			}
		},
		//最终展示每页数据量
		size:{
			get(){
				return this.dsize || Number(this.pageSize);
			},
			set(val){}
		}
	},
	watch: {
		current(val) {
			this.currentIndex = +val
		}
	},
	created() {
		this.currentIndex = +this.current
	},
	methods: {
		clickLeft() {
			if (Number(this.currentIndex) === 1) {
				return
			}
			this.currentIndex -= 1
			this.change('prev')
		},
		clickRight() {
			if (Number(this.currentIndex) === this.maxPage) {
				return
			}
			this.currentIndex += 1
			this.change('next')
		},
		change(e) {
			this.$emit('change', {
				type: e,
				current: this.currentIndex,
				size:this.size
			})
		},
		onSelectorClick(index){
			if(this.size === this.sizes[index]){
				
			}else{
				this.dsize = this.sizes[index]
				this.change('size');
			}
			// this.showSizes =  false;
			this.closeSelect()
		},
		closeSelect(){
			this.$store.commit('changePopshow',{
				tmpTableId:this.tmpTableId,
				popname:''
			})
		},
		showSelectFun(){
			console.log(this.showSizes)
			if(this.showSizes){
				this.closeSelect()
			}else{
				this.$store.commit('changePopshow',{
					tmpTableId:this.tmpTableId,
					popname:'sizes'
				})
			
			}
				console.log(this.showSizes)
			// this.showSizes = !this.showSizes;
		},
		showCurrentFun(){
			if(this.showCurrent){
				this.closeSelect()
			}else{
				this.$store.commit('changePopshow',{
					tmpTableId:this.tmpTableId,
					popname:'current'
				})
			}
			// this.showCurrent = !this.showCurrent;
		},
		onCurrentConfirm(current){
			if(this.currentIndex === current){
				
			}else{
				this.currentIndex = current	
				this.change('current');		
			}
			this.closeSelect()
		}
	}
}
